import React, {ReactNode, useState} from 'react';
import '../App.css';

interface MyComponentProps {
    children: ReactNode;
}
export default function VeeamPage({children} : MyComponentProps) {
    const [isChecked, setIsChecked] = useState(false)

    function drawerToggle()
    {
        setIsChecked(!isChecked);
    }
    return (
        <div className="App">
            <div className="">
                <div className="drawer">
                    <input id="my-drawer" type="checkbox" className="drawer-toggle" checked={isChecked} onChange={drawerToggle}/>
                    <div className="drawer-content">
                        {/* Navbar */}
                        <div className="navbar bg-base-200 shadow shadow-blue-500">
                            <div className="flex-none">
                                <button className="btn btn-square btn-ghost" onClick={drawerToggle}>
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        className="inline-block h-5 w-5 stroke-current">
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            strokeWidth="2"
                                            d="M4 6h16M4 12h16M4 18h16"></path>
                                    </svg>
                                </button>
                            </div>
                            <div className="flex-1 h-fit w-fit object-contain">
                                <a href="/" className="btn btn-ghost text-xl"><img className="h-12 w-auto" alt="Dacom Logo" src="/Dacom_Logo.png"/></a>
                            </div>
                            <div className="flex-none">
                                <button className="btn btn-square btn-ghost">
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        className="inline-block h-5 w-5 stroke-current">
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            strokeWidth="2"
                                            d="M5 12h.01M12 12h.01M19 12h.01M6 12a1 1 0 11-2 0 1 1 0 012 0zm7 0a1 1 0 11-2 0 1 1 0 012 0zm7 0a1 1 0 11-2 0 1 1 0 012 0z"></path>
                                    </svg>
                                </button>
                            </div>
                        </div>

                        {/* Page content here bg-[#f2f5f8]*/}
                        <div className="xl:pl-32 xl:pr-32 lg:pl-24 lg:pr-24 md:pl-12 md:pr-12 sm:p-4 mt-12 ">
                            {children}
                        </div>



                    </div>
                    <div className="drawer-side">
                        <label htmlFor="my-drawer" aria-label="close sidebar" className="drawer-overlay"></label>

                        <ul className="menu bg-base-200 text-base-content min-h-full w-40 p-4">
                            {/* Sidebar content here */}
                            <li><a href="/">Veeam</a></li>
                            <li><a href="/">Datto</a></li>
                            <li><a href="/stats">Statistics</a></li>
                        </ul>

                    </div>
                </div>
            </div>
        </div>
    );
}
