import React, {useCallback, useEffect, useRef, useState} from 'react';
import { AgGridReact  } from "ag-grid-react";

import "ag-grid-community/styles/ag-grid.css"; // Mandatory CSS required by the Data Grid
import "ag-grid-community/styles/ag-theme-quartz.css";
import "./gridstyle.css";
export default function VeeamGrid()
{
    const gridRef = useRef<AgGridReact>(null);
    const [rowData, setRowData] = useState([]);

    useEffect(() => {
        const rep = fetch("http://localhost/api/data")
            .then(response => response.json())
            .then(data => setRowData(data))

    },[]);

    // Column Definitions: Defines the columns to be displayed.
    const [colDefs, setColDefs]: any = useState([
        { field: "vbrHostName", headerName: "VBR Server"},
        { field: "bHostName", headerName: "Host"},
        { field: "latestDate", headerName: "Last Restore Point"},
        { field: "parentJob" },
        { field: 'status'}
    ]);

    const onBtnExport = useCallback(() => {
        gridRef.current!.api.exportDataAsCsv();
    }, []);

    const rowClassRules = {
        'status-bad': (params: any) => { return params.data.status === "Error"; },
    };

    return (


        <div className="mx-auto justify-center xl:w-[1005px]">
            <div className="flex justify-end">
            <button className="btn btn-primary btn-xs" onClick={onBtnExport}>Export to CSV</button>
            </div>
                <div
                    className="ag-theme-quartz justify-center h-[500px] mt-1">
                    <AgGridReact
                        rowData={rowData}
                        columnDefs={colDefs}
                        domLayout="normal"
                        ref={gridRef}
                        rowClassRules={rowClassRules}
                    />
                </div>
        </div>


    );

}